import * as L from 'leaflet';
import * as React from 'react';
import '../styles/map.css';
import { MyPosition } from 'src/types/Position';
import green from 'src/map-marker-green.png';
import red from 'src/map-marker-red.png';
import yellow from 'src/map-marker-yellow.png';
interface IMap{
    positions:MyPosition[];
    mounted:boolean;
}


export class MapComponent extends React.Component<IMap>{
    map:L.Map; 
    markers:L.LayerGroup; 
    routing:L.Routing.Control[];
    state:{positions:MyPosition[]};
    attribution='&copy; <a href="http://osm.org/copyright">OpenStreetMap</a> contributors';

    constructor(props:IMap){
        super(props);
        
    }

    componentDidMount(){
        this.map= new L.Map('map',{
            zoom:7, 
            layers:[
            L.tileLayer("http://{s}.tile.osm.org/{z}/{x}/{y}.png",{
            attribution:this.attribution
            })]
            });
        this.markers=new L.LayerGroup();
        this.markers.addTo(this.map);
        this.routing=[];
        if(this.props.mounted){    
            this.setUpMap();
        }
    }

    setUpMap(){
        
        var positions:MyPosition[]=this.props.positions;
        this.map.setView(new L.LatLng(positions[0].lat,positions[0].lon),7);
        positions.map(position=> {
                let iconMarker;
                let state;
                let color;
                if(position.state=="out-of-range-temperature"){
                    iconMarker=L.icon({
                        iconUrl:yellow,
                        iconSize:     [36, 38],
                        iconAnchor:   [18, 48]
                    });
                    state="Temperature out of range";
                    color="#ffbc2b";
                }else if(position.state=="opened-unit"){
                    iconMarker=L.icon({
                        iconUrl:red,
                        iconSize:     [36, 48],
                        iconAnchor:   [18, 48]
                    });   
                    state="Opened Unit";
                    color="#ba1212";
                }else{
                    iconMarker=L.icon({
                        iconUrl:green,
                        iconSize:     [36, 48],
                        iconAnchor:   [18, 48]
                    });   
                    state="Ok";
                    color="#2ac406";
                }
                let popup=L.popup().setContent("<p style=\"font-weight:bold\">"+new Date(position.timestamp).toLocaleString()+"</p>"+"<p style=\"color:"+color+"; font-weight:bold\">"+state+"</p>");
                new L.Marker(new L.LatLng(position.lat,position.lon),{icon:iconMarker}).addTo(this.markers).bindPopup(popup);
            });
        
    }

    componentDidUpdate(prevProps:IMap){
        if(prevProps.positions!=this.props.positions){
            this.markers.clearLayers();
            this.setState({positions:this.props.positions});
        }
        if(this.props.mounted){
            this.setUpMap();
        }else{
            this.map.setView(new L.LatLng(41.881868, 12.503603),4);
        }
    }
        
    render(){    
        var style:React.CSSProperties={
            height:"500px"
         };
        return(
                <div style={style} id="map"></div>
           );     
    }

}


