import axios from 'axios';
const baseURL:string='https://zhwekpmhvk.execute-api.us-east-1.amazonaws.com/dev/data';


export async function  getPositions(id:number,callback:Function){
    await axios.get(baseURL+'?type=positions&id='+id).then((response)=>{
        if(response.status===200 && response.data!='[]'){
            callback(response);
        }else{
            response.data="notfound";
            callback(response);
        }
        }
    );
    
}

export async function getEvents(id:number,callback:Function){
    await axios.get(baseURL+'?type=events&id='+id).then((response)=>{
        if(response.status===200 && response.data!='[]'){
            callback(response);
        }else{
            response.data="notfound";
            callback(response);
        }
        }
    );
    
}
export async function getDevices(callback:Function){
    await axios.get(baseURL+'?type=devices').then((response)=>{
            if(response.status===200 && response.data!='[]'){
                callback(response);
            }else{
                response.data="notfound";
                callback(response);
            }
        }
    );
} 








